import React from 'react';
import Lens from './Lens.js';

function Popup({ fileName, onClose }) {
  const [isVisible, setIsVisible] = React.useState(0);
  const [isLensActive, setIsLensActive] = React.useState(false);
  const [isLensOn, setIsLensOn] = React.useState(false);
  const [xMouse, setXMouse] = React.useState(0.5);
  const [yMouse, setYMouse] = React.useState(0.5);
  const [startXPic, setStartXPic] = React.useState(0);
  const [startYPic, setStartYPic] = React.useState(0);
  const [widthPic, setWidthPic] = React.useState(0);
  const [heightPic, setHeightPic] = React.useState(0);
  const [browserWidth, setBrowserWidth] = React.useState(1024);
  const [browserHeight, setBrowserHeight] = React.useState(800);

  React.useEffect(() => {
    setBrowserWidth(window.innerWidth);
    setBrowserHeight(window.innerHeight);

    window.addEventListener('resize', handleWindowResize);
    return (() => {
      window.removeEventListener('resize', handleWindowResize);
    })
  }, []);



  React.useEffect(() => {
    setIsVisible(1);
    const imageEl = document.querySelector('.popup__image');
    setStartXPic(imageEl.getBoundingClientRect().x);
    setStartYPic(imageEl.getBoundingClientRect().y);
    setWidthPic(imageEl.getBoundingClientRect().width);
    setHeightPic(imageEl.getBoundingClientRect().height);

  }, [browserWidth, browserHeight])

  const handleWindowResize = () => {
    setBrowserWidth(window.innerWidth);
    setBrowserHeight(window.innerHeight);
  }

  const handleLensButton = () => {
    if (isLensOn) {
      setIsLensOn(false);
      document.documentElement.style.cursor = 'auto';
    } else {
      setIsLensOn(true);
      const cursorMagnifier = require('../images/search.png');
      // console.log(ccc);
      document.documentElement.style.cursor = `url('${cursorMagnifier.default}'), auto`; //важно!!! не забыть auto!!!
      // document.documentElement.style.cursor = `url('${magCursorPath}'), auto`; //важно!!! не забыть auto!!!
    }

  }

  const handleMouseEnter = () => {
    setIsLensActive(true);
    console.log('mouseEnter');
  }

  const handleMouseLeave = () => {
    setIsLensActive(false);
    console.log('mouseLeave');
  }

  const handleMouseMove = (evt) => {
    setXMouse((evt.pageX - evt.target.getBoundingClientRect().x) / evt.target.getBoundingClientRect().width);
    setYMouse((evt.pageY - evt.target.getBoundingClientRect().y) / evt.target.getBoundingClientRect().height);

    //console.log(xMouse, yMouse);
  }

  return (
    <div className="popup" style={{ opacity: isVisible }}>
      <div className="popup__overlay">
      </div>
      <div className="popup__container">

        <div className="popup__side">
          <p></p>
          <button className="popup__button">left</button>
        </div>
        <div className="popup__center">
          <img
            src={fileName}
            onMouseEnter={handleMouseEnter}
            onMouseLeave={handleMouseLeave}
            onMouseMove={handleMouseMove}
            className="popup__image"
            alt="" />

        </div>
        <div className="popup__side">
          <div className="popup__button-block">
            <button className="popup__button popup__button_type_close" onClick={onClose}>close</button>
            <button className="popup__button popup__button_type_magnify" onClick={handleLensButton}>magnify</button>
          </div>

          <button className="popup__button">right</button>
        </div>
        <p className="popup__text">подпись</p>
      </div>

      {(isLensActive && isLensOn) && <Lens
        fileName={fileName}
        xMouse={xMouse}
        yMouse={yMouse}
        startXPic={startXPic}
        startYPic={startYPic}
        widthPic={widthPic}
        heightPic={heightPic}
      />}

    </div>
  )

}

export default Popup;