import React from 'react';

function Lens({ fileName, xMouse, yMouse, startXPic, startYPic, widthPic, heightPic, onClose }) {
  const lensShape = require('../images/lensBig.png');
  const lensSize = Math.max(widthPic, heightPic);
  console.log(widthPic, heightPic, lensSize);
  const lensStyle = {
    position: 'fixed',
    top: `${startYPic}px`,
    left: `${startXPic}px`,
    width: `${.7 * lensSize}px`,
    height: `${.7 * lensSize}px`,
    transform: `translate( ${xMouse * widthPic - 0.22 * lensSize}px, ${yMouse * heightPic - 0.22 * lensSize}px)`,
    // backgroundColor: 'red',
    backgroundImage: `url('${lensShape.default}')`,
    backgroundSize: 'contain',
    backgroundRepeat: 'no-repeat',
  }
  const lensWindowStyle = {
    position: 'absolute',
    top: `${0.03 * lensSize}px`,
    left: `${0.03 * lensSize}px`,
    borderRadius: '50%',
    width: `${.415 * lensSize}px`,
    height: `${.415 * lensSize}px`,
    overflow: 'hidden',
  }
  const lensImageStyle = {
    position: 'absolute',
    top: '0px',
    left: '0px',
    transform: `translate( 
      ${- xMouse * window.innerWidth * 1.2 + 0.22 * lensSize}px, 
      ${- yMouse * (heightPic / widthPic) * window.innerWidth * 1.2 + 0.22 * lensSize}px)`,

    // position: 'absolute',
    // top: `${0.03 * widthPic}px`,
    // left: `${0.03 * widthPic}px`,
    // borderRadius: '50%',
    // width: `${.415 * widthPic}px`,
    // height: `${.415 * widthPic}px`,
  }
  // console.log(-xMouse * window.innerWidth);
  return (
    <div className="lens" style={lensStyle}>
      {/* <p className='lens__text'>{xMouse}, {yMouse}</p> */}
      <div style={lensWindowStyle}>
        <img src={fileName} className="lens__image" style={lensImageStyle} alt="" />
      </div>

    </div>

  )

}

export default Lens;